#design-by {
  position: relative;
  top: -200px;
}
#design-by .design-container {
  position: relative;
  background: white;
  overflow: hidden;
  border-radius: 5px;
}
#design-by .design-container .bg-container {
  position: relative;
  background: #6066f2;
  overflow: hidden;
  padding: 45px;
}
#design-by .design-container:after {
  content: "";
  display: table;
  clear: both;
}
#design-by .design-container .bg-image {
  position: absolute;
  z-index: 1;
  width: 600px;
  opacity: 0.3;
  right: -200px;
  top: -100px;
}
#design-by .design-container .info-section {
  background: #6066f2;
  width: 40%;
  display: inline-block;
  float: left;
}
#design-by .design-container .info-section h5 {
  color: #fea83b;
  font-size: 1.4rem;
  text-transform: uppercase;
  margin-bottom: 2rem;
  position: relative;
  z-index: 2;
}
#design-by .design-container .info-section h3 {
  font-size: 2.7rem;
  color: white;
  margin-bottom: 2rem;
  position: relative;
  z-index: 2;
}
#design-by .design-container .info-section .info-list {
  margin-bottom: 4rem;
  position: relative;
  z-index: 2;
}
#design-by .design-container .info-section .info-list li {
  list-style: none;
  color: white;
  font-size: 1rem;
  font-weight: 400;
  margin-bottom: 14.5px;
  vertical-align: top;
}
#design-by .design-container .info-section .info-list li .circle {
  background: #fea83b;
  display: inline-block;
  border-radius: 50%;
  height: 26px;
  width: 26px;
  margin-right: 10px;
  position: relative;
  top: 7px;
}
#design-by .design-container .info-section .info-list li .circle .fas {
  position: absolute;
  top: 6px;
  left: 5px;
}
#design-by .design-container .info-section .white-btn {
  background: white;
  border-radius: 50px;
  padding: 20px 65px;
  text-decoration: none;
  color: #6066f2;
  font-weight: 700;
  font-size: 1rem;
  display: inline-block;
  position: relative;
  z-index: 2;
}
#design-by .design-container .icon-section {
  width: 60%;
  display: inline-block;
  float: left;
  background: white;
  position: relative;
}
#design-by .design-container .icon-section .refresh-circle {
  position: absolute;
    width: 80px;
    height: 80px;
    background: white;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
#design-by .design-container .icon-section .refresh-circle .fas {
  position: relative;
  color: #fea83b;
  font-size: 2.5rem;
  top: 20px;
  left: 20px;
}
#design-by .design-container .icon-section .icon-box {
  width: 50%;
  display: inline-block;
  float: left;
  height: 226px;
  text-align: center;
  border: 1px solid #f4f5f7;
  color: #6066f2;
  font-size: 1.3rem;
}
#design-by .design-container .icon-section .icon-box .fas,
#design-by .design-container .icon-section .icon-box .far {
  font-size: 3rem;
  margin-bottom: 2rem;
  margin-top: 61px;
}
/*Laptop Devices*/
@media only screen and (max-width: 1215px) {
}
/*Tablet Devices*/
@media only screen and (max-width: 1023px) {
  #design-by .design-container .icon-section .refresh-circle .fas {
    font-size: 2.25rem;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
#design-by .design-container .icon-section .icon-box .fas, #design-by .design-container .icon-section .icon-box .far {
  font-size: 2rem;
}
}
/*Mobile devices*/
@media only screen and (max-width: 767px) {
  #design-by .design-container .info-section {
    width: 100%;
  }
  #design-by .design-container .icon-section {
    width: 100%;
  }
  #design-by .design-container .bg-container {
    padding: 20px;
  }
  #design-by .design-container .info-section .info-list li {
    line-height: 2;
  }
}
@media only screen and (max-width: 360px) {
  #design-by .design-container .icon-section .icon-box .title {
    font-size: 1.2rem;
  }
}

@media only screen and (max-width: 320px) {
  #design-by .design-container .info-section .white-btn {
    left: 50%;
    transform: translateX(-50%);
  }
}